#founder {
    min-height: 40vh;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-bottom: calc($spacing-xxl * 2);

    @media (max-width: $breakpoint-xs) {
        padding: 0 $spacing-md;
    }

    @media (max-width: $breakpoint-lg) {
        flex-direction: column;

        .image {
            position: initial;
            order: 1;
            clip-path: initial;
            width: 100%;
            height: 250px;
            margin: 0 0 $spacing-lg;
            border-radius: $spacing;
            box-shadow: 0 4px 6px #0000001f, 0 5px 15px #00000023;
            background: url('/media/home/found.webp') right center/cover no-repeat;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: top;
            }
        }

        .text {
            order: 2;
        }
    }

    .image {
        clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
        background: url('/media/home/found.webp') right center/cover no-repeat;
        height: 55vh;
        width: 500px;
    }

    h4 {
        line-height: 1.2;
    }

    h5 {
        margin: 0 0 $spacing-xs;
    }

    .verse {
        font-weight: 500;
    }

    p {
        &:nth-child(5) {
            font-weight: 600;
        }

        &:not(:nth-child(5)) {
            margin: 0 0 $spacing-xs;
        }
    }

    a {
        background: var(--picton-blue);
        padding: $spacing-xs $spacing;
    }

    .text {
        max-width: 65ch;
    }
}